// VER SI LUEGO CAMBIO LOS NOMBRES:
export async function getAllPokemon(url) {
  return new Promise((resolve) => {
    fetch(url)
      .then(res => res.json())
      .then(data => {
        resolve(data);
      });
  });
}

export async function getPokemon(url) {
  return new Promise((resolve) => {
    fetch(url)
      .then(res => res.json())
      .then(data => {
        resolve(data);
      });
  });
}
